.help-section {
  background-color: #0e0e0e;
  padding: 60px 20px;
  color: #ffffff;
  text-align: center;
  border-top: 2px solid #00ffff;
}

.help-heading {
  font-size: 2.5rem;
  color: #00ffff;
  text-shadow: 0 0 10px #00ffffaa;
  margin-bottom: 10px;
}

.help-subtitle {
  color: #ccc;
  margin-bottom: 30px;
}

.tab-buttons {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 15px;
  margin-bottom: 30px;
}

.tab-btn {
  background: transparent;
  border: 2px solid #00ffff;
  color: #00ffff;
  padding: 10px 20px;
  border-radius: 25px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s;
}

.tab-btn.active,
.tab-btn:hover {
  background-color: #00ffff;
  color: #0e0e0e;
  transform: scale(1.05);
  box-shadow: 0 0 15px #00ffff;
}

.search-box input {
  padding: 12px 20px;
  border-radius: 25px;
  border: 2px solid #00ffff;
  width: 60%;
  max-width: 400px;
  background-color: transparent;
  color: white;
  font-size: 1rem;
  margin-bottom: 20px;
  outline: none;
}

.help-results {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
}

.result-item {
  background: #1c1c1c;
  padding: 15px 20px;
  width: 80%;
  max-width: 500px;
  border-radius: 10px;
  box-shadow: 0 0 10px #00ffff55;
  transition: transform 0.3s;
}

.result-item:hover {
  transform: scale(1.02);
  box-shadow: 0 0 15px #00ffff;
}

.no-result {
  color: #ff6666;
  font-style: italic;
}
